<template>
  <div class="typeManage admin-layout">
    <div class="header">
      <el-form :model="form" ref="form" inline>
        <el-form-item label="编码" prop="code">
          <el-input v-model="form.code"></el-input>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="btns mb20">
      <el-button type="primary" @click="add">新增</el-button>
      <el-button type="success" @click="edit">编辑</el-button>
      <el-button type="danger" @click="del">删除</el-button>
    </div>
    <div class="main">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-card>
            <el-tree
              :data="treeData"
              :props="defaultProps"
              node-key="id"
              :current-node-key="curNode.id"
              highlight-current
              @node-click="handleNodeClick"
            ></el-tree>
          </el-card>
        </el-col>
        <el-col :span="18">
          <el-card>
            <div slot="header">
              {{ curNode.name || "未选择节点" }}
            </div>
            <el-table border :data="tableData" ref="tableData">
              <el-table-column label="序号" type="index" align="center" width="50px" />
              <el-table-column label="编码" prop="code" align="center" />
              <el-table-column label="名称" prop="name" align="center" />
            </el-table>
            <!-- <pagination
              :total="total"
              :page.sync="form.page"
              :limit.sync="form.pageSize"
              @pagination="getList"
            /> -->
          </el-card>
        </el-col>
      </el-row>
    </div>
    <el-dialog :visible.sync="show" :title="title" width="400px">
      <el-form :model="obj" label-width="60px">
        <el-form-item label="名称" prop="name">
          <el-input v-model="obj.name"></el-input>
        </el-form-item>
        <el-form-item label="编码" prop="code">
          <el-input v-model="obj.code"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button type="primary" @click="confirm">确认</el-button>
        <el-button>取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'TypeManage',
  data () {
    return {
      form: {
        code: '',
        name: ''
        // page: 1,
        // pageSize: 10
      },
      total: 0,
      tableData: [
        {
          code: '编码',
          name: '名称',
          id: '',
          parent: ''
        }
      ],
      treeData: [
        {
          id: '',
          name: '所有类型',
          code: 'all'
        }
      ],
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      // 弹窗框属性
      curNode: {},
      show: false,
      title: '新增',
      obj: {
        id: '',
        code: '',
        name: ''
      }
    }
  },
  methods: {
    search () {
      // this.form.page = 1
      this.getList()
    },
    reset () {
      this.$refs.form.resetFields()
    },
    getList () {
      console.log('getList')
    },
    add () {
      if (this.curNode) {
        this.obj.id = ''
        this.obj.name = ''
        this.obj.code = ''
        this.obj.parent = this.curNode.id
        this.title = '新增'
        this.show = true
      } else {
        this.$message.error('请选择父节点')
      }
    },
    check (row) {
      if (this.curNode) {
        this.obj.id = this.curNode.id
        this.obj.name = this.curNode.name
        this.obj.code = this.curNode.code
        this.obj.parent = this.curNode.parent
        this.title = '查看'
        this.show = true
        console.log('查看')
      } else {
        this.$message.error('请选择要查看的节点')
      }
    },
    edit (row) {
      if (this.curNode) {
        this.obj.id = this.curNode.id
        this.obj.name = this.curNode.name
        this.obj.code = this.curNode.code
        this.title = '编辑'
        this.show = true
        console.log('编辑')
      } else {
        this.$message.error('请选择要编辑的节点')
      }
    },
    del (row) {
      console.log('删除', row)
    },
    handleNodeClick (data) {
      this.curNode = data
      this.form.page = 1
      this.getList()
    },
    isArray (obj) {
      return Object.prototype.toString.call(obj) === '[object Array]'
    },
    confirm () {
      if (this.title === '新增') {
        if (!this.isArray(this.curNode.children)) {
          this.$set(this.curNode, 'children', [])
        }
        this.curNode.children.push(Object.assign({}, this.obj))
      } else if (this.title === '编辑') {
        this.curNode.id = this.obj.id
        this.curNode.name = this.obj.name
        this.curNode.code = this.obj.code
      }
      this.show = false
    }
  }
}
</script>
<style lang="scss" scoped>
.typeManage {
  .btns {
    margin-bottom: 20px;
  }
}
</style>
